import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';
import { Router, ActivatedRoute } from '@angular/router';
import { Tip } from '../core/tip';

@Component({
  selector: 'app-tip',
  templateUrl: './tip.component.html',
  styleUrls: ['./tip.component.css']
})
export class TipComponent implements OnInit {

  public currentTip: Tip = new Tip();
  public tipID: string;
  private tipReference: AngularFireObject<any>;

  ngOnInit() { }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase
  ) {
    this.getTip();
  }

  getTip(): void {
    this.tipID = this.route.snapshot.paramMap.get('key');
    this.tipReference = this.afDb.object('tips/' + this.tipID);
    this.tipReference.snapshotChanges().subscribe(action => {
      this.currentTip = action.payload.val() as Tip;
    });
  }

  onBack() {
    this.router.navigate(['/tips']);
  }
}
