import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { User } from '../core/user';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-consultants',
  templateUrl: './consultants.component.html',
  styleUrls: ['./consultants.component.css']
})
export class ConsultantsComponent implements OnInit {

  public tableData: TableData;
  public consultantsList: User[];
  private usersList: AngularFireList<any>;

  constructor(
    private afDb: AngularFireDatabase
  ) { }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Name', 'Email', 'City', 'Region']
    };
    this.usersList = this.afDb.list('users', ref => ref.orderByChild('role').equalTo('consultant'));
    this.usersList.snapshotChanges().subscribe(item => {
      this.consultantsList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['uid'] = element.key;
        this.consultantsList.push(y as User);
      });
    });
  }
}
