import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Production } from '../core/production';
import { Farm } from '../core/farm';

@Component({
  selector: 'app-edit-production',
  templateUrl: './edit-production.component.html',
  styleUrls: ['./edit-production.component.css']
})
export class EditProductionComponent implements OnInit {

  public editedProduction: Production = new Production();
  public productionID: string;
  private productionReference: AngularFireObject<any>;
  public farmerID: string;
  public farmsList: Farm[];

  ngOnInit() { }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
    this.productionID = this.route.snapshot.paramMap.get('key');
    this.getProduction();
    this.getFarms();
  }

  getProduction(): void {
    this.productionReference = this.afDb.object('production/' + this.productionID);
    this.productionReference.snapshotChanges().subscribe(action => {
      this.editedProduction = action.payload.val() as Production;
    });
  }

  getFarms() {
    this.contentService.getFarmsData().snapshotChanges().subscribe(item => {
      this.farmsList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['fid'] = element.key;
        if (this.farmerID === (y as Farm).farmerid) {
          this.farmsList.push(y as Farm);
        }
      });
    });
  }

  onSubmit(form: NgForm) {
    if (form.value.pid !== null) {
      this.contentService.updateProduction(form.value);
    }
    this.router.navigate(['/production']);
  }

  onCancel() {
    this.router.navigate(['/production']);
  }
}
