import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Farm } from '../core/farm';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';

@Component({
  selector: 'app-new-production',
  templateUrl: './new-production.component.html',
  styleUrls: ['./new-production.component.css']
})
export class NewProductionComponent implements OnInit {

  public farmerID: string;
  public farmsList: Farm[];
  faList: AngularFireList<any>;

  ngOnInit() { }

  constructor(
    private router: Router,
    private afDb: AngularFireDatabase,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
    this.getFarms();
    this.contentService.getProductionStatistics();
  }

  getFarms() {
    this.faList = this.afDb.list('farms', ref => ref.orderByChild('farmerid').equalTo(this.farmerID));
    this.faList.snapshotChanges().subscribe(item => {
      this.farmsList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['fid'] = element.key;
        this.farmsList.push(y as Farm);
      });
    });
  }

  onSubmit(form: NgForm) {
    this.contentService.insertProduction(form.value);
    this.router.navigate(['/production']);
  }

  onCancel() {
    this.router.navigate(['/production']);
  }
}
